'use client';

import Image from 'next/image';
import Link from 'next/link';
import { ArrowRight, Award, Building, TrendingUp } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { motion } from 'framer-motion';

export function AnimatedAboutSection() {
  return (
    <section className="section bg-gray-50 overflow-hidden">
      <div className="container">
        <div className="grid grid-cols-1 gap-12 md:grid-cols-2">
          <motion.div
            initial={{ x: -200, opacity: 0 }}
            whileInView={{ x: 0, opacity: 1 }}
            viewport={{ margin: "-100px", once: true }}
            transition={{
              duration: 0.5,
              ease: [0.15, 0.1, 0.15, 0.5],
              type: "spring",
              stiffness: 20
            }}
            className="relative overflow-hidden rounded-lg"
          >
            <Image
              src="/images/luxury-home-2.jpg"
              alt="Luxury RE property"
              fill
              className='w-full'
            />
          </motion.div>
          <motion.div
            initial={{ x: 200, opacity: 0 }}
            whileInView={{ x: 0, opacity: 1 }}
            viewport={{ margin: "-100px", once: true }}
            transition={{
              duration: 1,
              ease: [0.25, 0.1, 0.25, 1],
              type: "spring",
              stiffness: 50
            }}
            className="flex flex-col justify-center"
          >
            <motion.h2
              initial={{ y: 50, opacity: 0 }}
              whileInView={{ y: 0, opacity: 1 }}
              viewport={{ margin: "-100px", once: true }}
              transition={{
                duration: 0.75,
                delay: 0.25,
                ease: [0.25, 0.1, 0.25, 1]
              }}
              className="heading-lg cursor-default"
            >
              About TBD RE Strategies
            </motion.h2>
            <motion.p
              initial={{ y: 50, opacity: 0 }}
              whileInView={{ y: 0, opacity: 1 }}
              viewport={{ margin: "-100px", once: true }}
              transition={{
                duration: 0.75,
                delay: 0.5,
                ease: [0.25, 0.1, 0.25, 1]
              }}
              className="mb-6 text-lg text-gray-700 cursor-default"
            >
              At TBD RE Strategies, we specialize in loss mitigation and high-ROI investment opportunities in the real estate market. With years of expertise, we've helped clients navigate complex real estate scenarios and maximize their returns.
            </motion.p>
            <motion.p
              initial={{ y: 50, opacity: 0 }}
              whileInView={{ y: 0, opacity: 1 }}
              viewport={{ margin: "-100px", once: true }}
              transition={{
                duration: 0.75,
                delay: 0.75,
                ease: [0.25, 0.1, 0.25, 1]
              }}
              className="mb-6 text-lg text-gray-700 cursor-default"
            >
              Our approach combines deep market knowledge, innovative strategies, and a client-first mindset to deliver exceptional results for investors at every level.
            </motion.p>
            <motion.div
              initial={{ y: 50, opacity: 0 }}
              whileInView={{ y: 0, opacity: 1 }}
              viewport={{ margin: "-100px", once: true }}
              transition={{
                duration: 0.75,
                delay: 1,
                ease: [0.25, 0.1, 0.25, 1]
              }}
              className="grid grid-cols-1 gap-4 sm:grid-cols-2"
            >
              <motion.div
                whileHover={{ scale: 1.05 }}
                transition={{ duration: 0.15 }}
                className="flex items-center gap-2"
              >
                <Award className="h-5 w-5 text-primary" />
                <span className="font-medium cursor-default">Expert Analysis</span>
              </motion.div>
              <motion.div
                whileHover={{ scale: 1.05 }}
                transition={{ duration: 0.15 }}
                className="flex items-center gap-2"
              >
                <TrendingUp className="h-5 w-5 text-primary" />
                <span className="font-medium cursor-default">High-ROI Strategies</span>
              </motion.div>
              <motion.div
                whileHover={{ scale: 1.05 }}
                transition={{ duration: 0.15 }}
                className="flex items-center gap-2"
              >
                <Building className="h-5 w-5 text-primary" />
                <span className="font-medium cursor-default">Property Optimization</span>
              </motion.div>
            </motion.div>
            <motion.div
              initial={{ y: 50, opacity: 0 }}
              whileInView={{ y: 0, opacity: 1 }}
              viewport={{ margin: "-100px", once: true }}
              transition={{
                duration: 0.75,
                delay: 1.25,
                ease: [0.25, 0.1, 0.25, 1]
              }}
            >
              <Button asChild className="mt-8 w-fit">
                <Link href="/about">
                  Learn More <ArrowRight className="ml-2 h-4 w-4" />
                </Link>
              </Button>
            </motion.div>
          </motion.div>
        </div>
      </div>
    </section>
  );
}
