'use client';

import Link from 'next/link';
import { ArrowRight, Award, Building, TrendingUp } from 'lucide-react';
import { motion } from 'framer-motion';

export function AnimatedServicesSection() {
  return (
    <section className="section">
      <div className="container">
        <motion.div
          initial={{ y: 100, opacity: 0 }}
          whileInView={{ y: 0, opacity: 1 }}
          viewport={{ margin: "-100px", once: true }}
          transition={{ 
            duration: 1,
            ease: [0.15, 0.1, 0.15, 0.5],
            type: "spring",
            stiffness: 10
          }}
          className="mx-auto mb-12 max-w-3xl text-center"
        >
          <h2 className="heading-lg">Our Services</h2>
          <p className="text-lg text-gray-700">
            We offer specialized services designed to maximize your real estate investments through innovative strategies and expert market knowledge.
          </p>
        </motion.div>
        <div className="grid grid-cols-1 gap-8 md:grid-cols-3">
          <motion.div
            initial={{ y: 100, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            viewport={{ margin: "-100px", once: true }}
            transition={{ 
              duration: 0.75,
              delay: 0.25,
              ease: [0.25, 0.1, 0.25, 1]
            }}
            whileHover={{ y: -10 }}
            className="rounded-lg border border-gray-200 bg-white p-6 shadow-sm transition-all hover:shadow-md"
          >
            <div className="mb-4 flex h-12 w-12 items-center justify-center rounded-full bg-primary/10 text-primary">
              <Building className="h-6 w-6" />
            </div>
            <h3 className="mb-3 text-xl font-bold">Loss Mitigation</h3>
            <p className="mb-4 text-gray-600">
              Expert strategies to minimize losses and protect your real estate investments during market downturns.
            </p>
            <Link
              href="/services"
              className="inline-flex items-center text-sm font-medium text-primary hover:underline"
            >
              Learn More <ArrowRight className="ml-1 h-4 w-4" />
            </Link>
          </motion.div>
          <motion.div
            initial={{ y: 100, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            viewport={{ margin: "-100px", once: true }}
            transition={{ 
              duration: 0.75,
              delay: 0.5,
              ease: [0.25, 0.1, 0.25, 1]
            }}
            whileHover={{ y: -10 }}
            className="rounded-lg border border-gray-200 bg-white p-6 shadow-sm transition-all hover:shadow-md"
          >
            <div className="mb-4 flex h-12 w-12 items-center justify-center rounded-full bg-primary/10 text-primary">
              <TrendingUp className="h-6 w-6" />
            </div>
            <h3 className="mb-3 text-xl font-bold">Investment Strategies</h3>
            <p className="mb-4 text-gray-600">
              Customized high-ROI investment approaches tailored to your financial goals and risk tolerance.
            </p>
            <Link
              href="/services"
              className="inline-flex items-center text-sm font-medium text-primary hover:underline"
            >
              Learn More <ArrowRight className="ml-1 h-4 w-4" />
            </Link>
          </motion.div>
          <motion.div
            initial={{ y: 100, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            viewport={{ margin: "-100px", once: true }}
            transition={{ 
              duration: 0.75,
              delay: 0.75,
              ease: [0.25, 0.1, 0.25, 1]
            }}
            whileHover={{ y: -10 }}
            className="rounded-lg border border-gray-200 bg-white p-6 shadow-sm transition-all hover:shadow-md"
          >
            <div className="mb-4 flex h-12 w-12 items-center justify-center rounded-full bg-primary/10 text-primary">
              <Award className="h-6 w-6" />
            </div>
            <h3 className="mb-3 text-xl font-bold">Property Consultation</h3>
            <p className="mb-4 text-gray-600">
              Expert analysis and advice on property selection, valuation, and optimization for maximum returns.
            </p>
            <Link
              href="/services"
              className="inline-flex items-center text-sm font-medium text-primary hover:underline"
            >
              Learn More <ArrowRight className="ml-1 h-4 w-4" />
            </Link>
          </motion.div>
        </div>
      </div>
    </section>
  );
}
